"use client";

import { useState } from "react";
import Link from "next/link";

import {
  Users, Clock, Home, UserCog, FileText, Cog, ClipboardList, Layers,
  FileCheck2, ClipboardSignature, ChevronLeft, ChevronRight,
  ChevronDown, ChevronUp
} from "lucide-react";

import { useSidebarStore } from "@/store/sidebarStore";

export default function Sidebar() {
  const { isExpanded, setIsExpanded, toggleSidebar } = useSidebarStore();
  const [openItem, setOpenItem] = useState<number | null>(null);

  const menuItems = [
    { title: "Dashboard", icon: Home, href: "/dashboard" },
    { title: "Employees", icon: Users, href: "/employees" },
    { title: "Manage Shifts", icon: Layers, href: "/shifts", badge: "New" },
    { title: "Leaves & Holidays", icon: ClipboardList, href: "/leaves" },
    { title: "Approval Requests", icon: FileCheck2, href: "/approvals" },
    { title: "Payroll", icon: FileText, href: "/payroll" },
    { title: "Loans & Advances", icon: ClipboardSignature, href: "/loans" },
    { title: "Reports", icon: FileText, href: "/reports" },
    { title: "Dynamic Reports", icon: FileText, href: "/reports/dynamic" },
    { title: "User Management", icon: UserCog, href: "/users" },
    { title: "Activity Logs", icon: Clock, href: "/logs" },
    { title: "Configuration", icon: Cog, href: "/config" },
  ];

  const handleGroupClick = (idx: number) => {
    setOpenItem(openItem === idx ? null : idx);
  };

  return (
    <aside
      className={`
        bg-white border-r h-screen py-5 px-3 overflow-y-auto
        transition-all duration-300
        ${isExpanded ? "w-64" : "w-20"}
      `}
      onMouseEnter={() => setIsExpanded(true)}
      onMouseLeave={() => setIsExpanded(false)}
    >
      {/* Header */}
      <div className="flex items-center justify-between px-2 mb-6">
        {isExpanded && <h2 className="text-lg font-semibold">Menu</h2>}

        <button
          onClick={(e) => {
            e.stopPropagation();
            toggleSidebar();
          }}
          className="p-1 rounded hover:bg-slate-200"
        >
          {isExpanded ? <ChevronLeft /> : <ChevronRight />}
        </button>
      </div>

      {/* Navigation */}
      <nav className="space-y-1">
        {menuItems.map((item, idx) => {
          const Icon = item.icon;
          const isOpen = openItem === idx;

          return (
            <div key={idx}>
              <div
                className="flex items-center justify-between px-3 py-2 rounded-md hover:bg-slate-100 cursor-pointer"
                onClick={() => handleGroupClick(idx)}
              >
                <div className="flex items-center gap-3">
                  <Icon className="h-5 w-5" />

                  {isExpanded && (
                    <Link 
                      href={item.href} 
                      className="text-sm font-medium"
                      onClick={(e) => e.stopPropagation()}
                    >
                      {item.title}
                    </Link>
                  )}
                </div>

                {isExpanded && (
                  item.badge ? (
                    <span className="text-xs bg-blue-600 text-white px-2 py-0.5 rounded-full">
                      {item.badge}
                    </span>
                  ) : isOpen ? (
                    <ChevronUp className="h-4 w-4 text-slate-600" />
                  ) : (
                    <ChevronDown className="h-4 w-4 text-slate-600" />
                  )
                )}
              </div>

              {isExpanded && isOpen && (
                <div className="ml-10 py-1 text-sm text-slate-500">
                  {/* future submenu */}
                </div>
              )}
            </div>
          );
        })}
      </nav>
    </aside>
  );
}
